.noam-nav{
  background-color:#fff;
  z-index: 100;
  padding-top:50px;
  // padding-bottom:22px;  
  
  @media  (max-width: @screen-xs-max) {
    padding-top:20px;
    // padding-bottom:15px;  
  }
  @media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {
    padding-top:20px;
    // padding-bottom:15px;  
  }
  .row:first-of-type{
    padding-bottom:22px;
  }
  .navbar-toggle{
    margin: 0 0 0 0;
    float:none;
    border:1px solid @gray-lighter;
  }
  .icon-bar{
    background-color:@gray;
  }
  .btn{
    padding-top: 6px;
    padding-bottom: 3px;
    line-height: 1;
  }
  .navbar-collapse{
    @media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {
      float:none !important; //important because we need to override the pull-right class  
    }
    @media (max-width:@screen-xs-max){
      text-align: center;
    }
    text-transform: uppercase;
    font-size: @font-size-small;
    font-weight:500;
    letter-spacing: 2px;
    line-height: 40px;
    padding-top: 10px;
    ul{
      list-style: none;
      padding:0;
      margin:0;
      li{
        // line-height: 1;
        display: inline-block;
        
      }
      a:link{
        text-decoration: none;
      }

      li+li:before{
        color: @gray-lighter;
        font-size: 11px;
        content: "|";
        padding: 0 14px;
        @media (max-width: @screen-md-max) {
          padding: 0 5px;          
        }        
      }

    }
    .current-menu-item a:not(.btn), a:hover:not(.btn){
      background-image:url(../../../../images/underline.png);
      background-position: 50% 100%;
      background-repeat: no-repeat;
      background-size: 55% 2px;
      padding-bottom: 10px;
      color: lighten(@link-color, 5%);
      transition:color 1s; 
    }
    

    @media  (max-width: @screen-xs-max) {
        float:none !important;
        padding-left: 0;
        padding-right: 0;
        li{
          display: block !important;
        }
        ul li+li:before{
          content: none;
          padding: 0;
        }
        a:link{
          display:block;
        }
        .current-menu-item a:not(.btn), a:hover:not(.btn){
          background:rgba(0,0,0,0.05);
          background-size:initial;
          padding-bottom: 0px;
          transition:background @transition-speed;
        }
        .btn{
          line-height:40px;
        }
    } 
  }
  .btn-nav{
    &.btn-xs{
      // padding:2px 24px 0px 24px;
      padding:6px 24px 2px 24px;    
    }
  
    @media (max-width: @screen-md-max) {  
      padding:6px 14px 2px 14px;    
    }
  }
}
    




